import React from "react";
import { useSelector, useDispatch } from "react-redux";

import { setSuccessOrder } from "../../../redux/slices/orderingSlice";

import ItemCheckbox from "../../itemCheckbox/ItemCheckbox";

import "./summary.scss";

const Summary = () => {
	const dispatch = useDispatch();
	const totalPrice = useSelector((state) => state.ordering.totalPrice);
	const deliveryPrice = useSelector((state) => state.ordering.deliveryPrice);
	const stepsStatus = useSelector((state) => state.ordering["obtaining method"]["stepsStatus"]);

	const [isAcceptedAgreement, setAcceptedAgreement] = React.useState(true);

	const [isFormStepsConfirmed, setFormStepsConfirmed] = React.useState(false);

	React.useEffect(() => {
		checkTotalFormConfiration();
	}, [stepsStatus]);

	const checkTotalFormConfiration = () => {
		const stepsStatusArr = Object.entries(stepsStatus);
		setFormStepsConfirmed(stepsStatusArr.every((item) => item[1] === "confirmed"));
	};

	const handleSubmit = () => {
		alert("Congratulation! Your order has been created successfully!");
		dispatch(setSuccessOrder());
	};

	return (
		<div className="summary">
			<div className="summary__body">
				<div className="summary__title">Total</div>
				<ul className="summary__items">
					<li className="summary__item">
						<div className="summary__item-text">Goods in the amount</div>
						<div className="summary__item-value summary__item-value--products">{totalPrice}</div>
					</li>
					<li className="summary__item">
						<div className="summary__item-text">Cost of delivery</div>
						<div className="summary__item-value summary__item-value--shipping">
							{totalPrice ? deliveryPrice : 0}
						</div>
					</li>
				</ul>
				<div className="summary__total">
					<div className="summary__total-text">To pay</div>
					<div className="summary__total-value">{totalPrice ? totalPrice + deliveryPrice : 0}</div>
				</div>
				<button
					className="summary__btn"
					disabled={!(isAcceptedAgreement && isFormStepsConfirmed)}
					onClick={handleSubmit}
				>
					Place an Order
				</button>
			</div>
			<div className="summary__agreement">
				<ItemCheckbox
					name="agreement"
					checked={isAcceptedAgreement}
					description="I accept the terms of the user agreement"
					handleChange={() => setAcceptedAgreement((state) => !state)}
				/>
			</div>
		</div>
	);
};

export default Summary;
