import React from "react";
import { Helmet } from "react-helmet";
import { useSelector, useDispatch } from "react-redux";
import * as Yup from "yup";

import { changeStepsStatus } from "../../redux/slices/orderingSlice";
import OrderingStep from "../../components/orderingStep/OrderingStep";

import {
	CartProducts,
	CartProductsPreview,
	ObtainingMethod,
	ObtainingMethodPreview,
	PaymentMethod,
	PaymentMethodPreview,
	Recipient,
	RecipientPreview,
	Summary,
} from "../../components/orderingFormSteps";

import "./orderingPage.scss";

const OrderingPage = () => {
	const dispatch = useDispatch();
	const stepsStatus = useSelector((state) => state.ordering["obtaining method"]["stepsStatus"]);

	const totalProductsQuantity = useSelector((state) => state.ordering.totalProductsQuantity);
	const userFormData = useSelector((state) => state.ordering["obtaining method"]["userFormData"]);
	const { minDate, maxDate } = useSelector((state) => state.ordering["obtaining method"]["availableDeliveryDates"]);
	const recipientData = useSelector((state) => state.ordering.recipient);

	const steps = [
		{
			id: 1,
			title: "Your order",
			childrenComponent: <CartProducts />,
			renderConfirmedPreview: () => <CartProductsPreview />,
			withButton: true,
		},
		{
			id: 2,
			title: "Obtaining method",
			childrenComponent: <ObtainingMethod />,
			renderConfirmedPreview: () => <ObtainingMethodPreview />,
			withButton: true,
			formConfig: {
				initialValues: userFormData,
				validationSchema: Yup.object().shape({
					country: Yup.object().nullable().required("*Required"),
					address: Yup.string()
						.min(10, "*Too short address!")
						.max(500, "*Too long address!")
						.required("*Required"),
					date: Yup.date()
						.typeError("*Invalid date")
						.min(minDate, `*Min date is ${new Date(minDate).toDateString()}`)
						.max(maxDate, `*Max date is ${new Date(maxDate).toDateString()}`),
				}),
			},
		},
		{
			id: 3,
			title: "Payment method",
			childrenComponent: <PaymentMethod />,
			renderConfirmedPreview: () => <PaymentMethodPreview />,
			withButton: true,
		},
		{
			id: 4,
			title: "Recipient",
			childrenComponent: <Recipient />,
			renderConfirmedPreview: () => <RecipientPreview />,
			withButton: true,
			buttonText: "Confirm",
			formConfig: {
				initialValues: recipientData,
				validationSchema: Yup.object().shape({
					firstName: Yup.string().min(2, "*Too short name!").max(50, "*Too long name!").required("*Required"),
					lastName: Yup.string()
						.min(2, "*Too short surname!")
						.max(50, "*Too long surname!")
						.required("*Required"),
					phoneNumber: Yup.string()
						.matches(
							/^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4,6}$/im,
							"Phone number is not valid"
						)
						.required("*Required"),
					email: Yup.string().email("*Email is not valid!").required("*Required"),
				}),
			},
		},
	];

	const handleCurrentStep = (id) => {
		if (id < steps.length) {
			dispatch(
				changeStepsStatus({
					...stepsStatus,
					[id]: "confirmed",
					[id + 1]: stepsStatus[id + 1] !== "confirmed" ? "editing" : "confirmed",
				})
			);
		} else {
			dispatch(
				changeStepsStatus({
					...stepsStatus,
					[id]: "confirmed",
				})
			);
		}
	};

	const handleEditStep = (id) => {
		dispatch(
			changeStepsStatus({
				...stepsStatus,
				[id]: "editing",
			})
		);
	};

	return (
		<>
			<Helmet>
				<title>Ordering - Smart Technics</title>
				<meta
					name="description"
					content={
						"It is a cart. You can add the products and change em' quantity. Fill the forms and make an order"
					}
				/>
			</Helmet>
			<main>
				<div className="container">
					<div className="page-ordering">
						<h3 className="page-ordering__title">Placing an order</h3>
						<div className="page-ordering__form">
							<div className="ordering">
								<div className="ordering__content">
									{steps?.length &&
										steps.map(({ id, childrenComponent, withButton, ...rest }) => (
											<div key={id} className="ordering__item">
												<OrderingStep
													status={stepsStatus[id]}
													withButton={totalProductsQuantity === 0 ? false : withButton}
													onSubmitStep={() => handleCurrentStep(id)}
													onEditStep={() => handleEditStep(id)}
													{...rest}
												>
													{childrenComponent}
												</OrderingStep>
											</div>
										))}
								</div>
								<div className="ordering__summary">
									<Summary />
								</div>
							</div>
						</div>
					</div>
				</div>
			</main>
		</>
	);
};

export default OrderingPage;
