import React from "react";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import { useSelector, useDispatch } from "react-redux";

import { setPaymentMethod } from "../../../redux/slices/orderingSlice";

import "./paymentMethod.scss";

const PaymentMethod = () => {
	const dispatch = useDispatch();
	const { methods, selectedMethod } = useSelector((state) => state.ordering["payment method"]);

	const handleChange = (event) => {
		dispatch(setPaymentMethod(event.target.value));
	};

	return (
		<div className="payment-method">
			<div className="payment-method__select">
				<Select
					className="select-payment-method"
					value={selectedMethod}
					onChange={handleChange}
					inputProps={{ "aria-label": "Without label" }}
				>
					{methods.map((item) => (
						<MenuItem className="select-payment-method__menu-item" key={item} value={item}>
							{item}
						</MenuItem>
					))}
				</Select>
			</div>
		</div>
	);
};

export default PaymentMethod;
