import React from "react";
import { useSelector } from "react-redux";

import "./obtainingMethodPreview.scss";

const ObtainingMethodPreview = () => {
	const { country, address, date, time } = useSelector((state) => state.ordering["obtaining method"]["userFormData"]);

	const dateString = new Intl.DateTimeFormat("en-US").format(new Date(date));
	const timeString = new Intl.DateTimeFormat("en-US", { hour12: true, hour: "numeric", minute: "numeric" }).format(
		new Date(time)
	);
	return (
		<div className="obtaining-method-filled">
			<div className="obtaining-method-filled__content">
				<div className="obtaining-method-filled__title">Delivery to {country?.label}</div>
				<div className="obtaining-method-filled__text">Address: {address}</div>
				<div className="obtaining-method-filled__working-hours">
					{dateString} {timeString}
				</div>
			</div>
		</div>
	);
};

export default ObtainingMethodPreview;
