import React from "react";
import { useSelector } from "react-redux";

import "./recipientPreview.scss";

const RecipientPreview = () => {
	const { firstName, lastName, phoneNumber, email, callback } = useSelector((state) => state.ordering.recipient);
	return (
		<div className="recipient-filled">
			<dl className="recipient-filled__list">
				<dt className="recipient-filled__item-title">Recipient name:</dt>
				<dd className="recipient-filled__item-description">
					{firstName} {lastName}
				</dd>
				<br />
				<dt className="recipient-filled__item-title">Phone number:</dt>
				<dd className="recipient-filled__item-description">{phoneNumber}</dd>
				<br />
				<dt className="recipient-filled__item-title">Email:</dt>
				<dd className="recipient-filled__item-description">{email}</dd>
				<br />
				<dt className="recipient-filled__item-title">Call me back:</dt>
				<dd className="recipient-filled__item-description">{callback ? "Yes" : "No"}</dd>
			</dl>
		</div>
	);
};

export default RecipientPreview;
