import React from "react";
import classNames from "classnames";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import { Formik, Form } from "formik";
import { useDispatch } from "react-redux";

import { setDeliveryData, setRecipientData } from "../../redux/slices/orderingSlice";

import "./orderingStep.scss";

const OrderingStep = ({
	title,
	status,
	children,
	withButton,
	buttonText,
	onSubmitStep,
	onEditStep,
	renderConfirmedPreview = () => null,
	formConfig,
}) => {
	const dispatch = useDispatch();

	const itemOrderingClassnames = classNames("item-ordering", {
		disabled: status === "hidden",
	});

	const handleFormSubmit = (value) => {
		const reduxTitle = title.toLowerCase();

		switch (reduxTitle) {
			case "obtaining method":
				dispatch(
					setDeliveryData({
						...value,
						date: new Date(value.date).toISOString(),
						time: new Date(value.time).toISOString(),
					})
				);
				break;
			case "recipient":
				dispatch(setRecipientData(value));
				break;
			default:
				break;
		}
		onSubmitStep();
	};

	return formConfig ? (
		<Formik
			initialValues={formConfig.initialValues}
			validationSchema={formConfig.validationSchema}
			onSubmit={handleFormSubmit}
		>
			{(props) => (
				<Form className={itemOrderingClassnames}>
					<div className="item-ordering__body">
						<div className="item-ordering__title">{title}</div>
						<div className="item-ordering__content">
							{
								<TransitionGroup>
									{status === "editing" && (
										<CSSTransition timeout={500} classNames="changeStepTransition">
											{children}
										</CSSTransition>
									)}
									{status === "confirmed" && (
										<CSSTransition timeout={500} classNames="changeStepTransition">
											<div className="item-ordering__filled">
												<div className="item-ordering__confirmed-preview">
													{renderConfirmedPreview()}
												</div>
												<button
													className="item-ordering__btn-change"
													type="button"
													onClick={onEditStep}
												>
													Edit
												</button>
											</div>
										</CSSTransition>
									)}
									{status === "hidden" && null}
								</TransitionGroup>
							}
						</div>
					</div>
					{status === "editing" && withButton && (
						<button className="item-ordering__btn-next" type="submit" onClick={props.handleSubmit}>
							{buttonText ? buttonText : "Next step"}
						</button>
					)}
				</Form>
			)}
		</Formik>
	) : (
		<div className={itemOrderingClassnames}>
			<div className="item-ordering__body">
				<div className="item-ordering__title">{title}</div>
				<div className="item-ordering__content">
					{
						<TransitionGroup>
							{status === "editing" && (
								<CSSTransition timeout={500} classNames="changeStepTransition">
									{children}
								</CSSTransition>
							)}
							{status === "confirmed" && (
								<CSSTransition timeout={500} classNames="changeStepTransition">
									<div className="item-ordering__filled">
										<div className="item-ordering__confirmed-preview">
											{renderConfirmedPreview()}
										</div>
										<button className="item-ordering__btn-change" onClick={onEditStep}>
											Edit
										</button>
									</div>
								</CSSTransition>
							)}
							{status === "hidden" && null}
						</TransitionGroup>
					}
				</div>
			</div>
			{status === "editing" && withButton && (
				<button className="item-ordering__btn-next" onClick={onSubmitStep}>
					{buttonText ? buttonText : "Next step"}
				</button>
			)}
		</div>
	);
};

export default OrderingStep;
