import React from "react";
import dayjs from "dayjs";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { TimePicker } from "@mui/x-date-pickers/TimePicker";
import { useFormikContext } from "formik";
import { useSelector } from "react-redux";

import "./customTimePicker.scss";

const CustomTimePicker = () => {
	const formik = useFormikContext();
	const minTime = useSelector((state) => state.ordering["obtaining method"]["availableDeliveryDates"]["minTime"]);

	const [time, setTime] = React.useState(minTime);

	const handleChange = (value) => {
		setTime(value);
		formik.setFieldValue("time", value);
	};

	const handleError = (value) => {
		formik.setFieldTouched("time", true);
		formik.setFieldError("time", value);
	};

	const handleBlur = () => {
		formik.setFieldTouched("time", true);
	};

	return (
		<LocalizationProvider dateAdapter={AdapterDayjs}>
			<TimePicker
				value={time}
				onChange={handleChange}
				onError={handleError}
				onBlur={handleBlur}
				InputAdornmentProps={{
					className: "custom-time-picker__inputAdornment",
				}}
				minutesStep={15}
				minTime={dayjs("2018-01-01T08:00")}
				maxTime={dayjs("2018-01-01T22:00")}
				renderInput={({ inputRef, inputProps, InputProps }) => (
					<div className="custom-time-picker">
						<input
							ref={inputRef}
							{...inputProps}
							className="custom-time-picker__input"
							placeholder="Choose a time"
							readOnly
						/>
						{InputProps?.endAdornment}
					</div>
				)}
			/>
		</LocalizationProvider>
	);
};

export default CustomTimePicker;
