import React from "react";
import { useSelector } from "react-redux";

import "./cartProductsPreview.scss";

const CartProductsPreview = () => {
	const productsInCart = useSelector((state) => state.ordering.productsInCart);
	return (
		<div className="order-filled">
			<ul className="order-filled__items">
				{productsInCart.map(({ productId, title, images }) => (
					<li className="order-filled__item" key={productId}>
						<img className="order-filled__img" src={images[0].src} alt={title} />
					</li>
				))}
			</ul>
		</div>
	);
};

export default CartProductsPreview;
