import React from "react";
import { Field, ErrorMessage } from "formik";

import CountrySelect from "../../countrySelect/CountrySelect";
import CustomDatePicker from "../../customDatePicker/CustomDatePicker";
import CustomTimePicker from "../../customTimePicker/CustomTimePicker";
import ItemInput from "../../itemInput/ItemInput";

import "./obtainingMethod.scss";

const ObtainingMethod = () => {
	return (
		<div className="obtaining-method">
			<ul className="obtaining-method__list">
				<li className="obtaining-method__item">
					<div className="item-input">
						<p className="item-input__title">Country</p>
						<div className="item-input__body">
							<CountrySelect />
						</div>
					</div>
				</li>
				<li className="obtaining-method__item">
					<ItemInput label="City, street, house/building/apartment number" name="address" />
				</li>
				<li className="obtaining-method__item">
					<div className="item-input">
						<p className="item-input__title">Date</p>
						<div className="item-input__body">
							<CustomDatePicker />
							<ErrorMessage className="item-input__error" name="date" component="div" />
						</div>
					</div>
				</li>
				<li className="obtaining-method__item">
					<div className="item-input">
						<p className="item-input__title">Time</p>
						<div className="item-input__body">
							<CustomTimePicker />
							<ErrorMessage className="item-input__error" name="time" component="div" />
						</div>
					</div>
				</li>
				<li className="obtaining-method__item">
					<div className="item-input">
						<p className="item-input__title">Message (optional)</p>
						<div className="item-input__body">
							<Field className="item-input__textarea" type="text" name="message" as="textarea" />
						</div>
					</div>
				</li>
			</ul>
		</div>
	);
};

export default ObtainingMethod;
