import React from "react";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { useFormikContext } from "formik";
import { useSelector } from "react-redux";

import "./customDatePicker.scss";

const CustomDatePicker = () => {
	const formik = useFormikContext();
	const { minDate: minDeliveryDate, maxDate: maxDeliveryDate } = useSelector(
		(state) => state.ordering["obtaining method"]["availableDeliveryDates"]
	);
	const [date, setDate] = React.useState(minDeliveryDate);

	const handleChange = (value) => {
		setDate(value);
		formik.setFieldValue("date", value);
	};

	const handleError = (value) => {
		formik.setFieldTouched("date", true);
		formik.setFieldError("date", value);
	};

	const handleBlur = () => {
		formik.setFieldTouched("date", true);
	};

	return (
		<LocalizationProvider dateAdapter={AdapterDayjs}>
			<DatePicker
				value={date}
				InputAdornmentProps={{
					className: "custom-date-picker__inputAdornment",
				}}
				name="date"
				maxDate={maxDeliveryDate}
				disablePast
				onChange={handleChange}
				onError={handleError}
				onBlur={handleBlur}
				renderInput={({ inputRef, inputProps, InputProps }) => (
					<div className="custom-date-picker">
						<input ref={inputRef} {...inputProps} className="custom-date-picker__input" />
						{InputProps?.endAdornment}
					</div>
				)}
			/>
		</LocalizationProvider>
	);
};

export default CustomDatePicker;
