import React from "react";
import { useSelector } from "react-redux";

import "./paymentMethodPreview.scss";

const PaymentMethodPreview = () => {
	const paymentMethod = useSelector((state) => state.ordering["payment method"]["selectedMethod"]);
	return (
		<div className="payment-method-filled">
			<p className="payment-method-filled__description">{paymentMethod}</p>
		</div>
	);
};

export default PaymentMethodPreview;
