import React from "react";
import { useFormikContext } from "formik";
import { useSelector } from "react-redux";

import ItemInput from "../../itemInput/ItemInput";
import ItemCheckbox from "../../itemCheckbox/ItemCheckbox";

import "./recipient.scss";

const Recipient = () => {
	const formik = useFormikContext();
	const callbackCheckbox = useSelector((state) => state.ordering.recipient.callback);

	return (
		<div className="item-client">
			<ul className="item-client__items">
				<li className="item-client__item">
					<ItemInput label="First name" name="firstName" placeholder="For instance, Nick" />
				</li>
				<li className="item-client__item">
					<ItemInput label="Last name" name="lastName" placeholder="For instance, Jackson" />
				</li>
				<li className="item-client__item">
					<ItemInput label="Phone number" name="phoneNumber" placeholder="+1__________" />
				</li>
				<li className="item-client__item">
					<ItemInput label="Email" name="email" placeholder="example@google.com" />
				</li>
			</ul>
			<div className="item-client__recall">
				<ItemCheckbox
					name="callback"
					checked={callbackCheckbox}
					description="Do not call me back for the order confirmation"
					handleChange={formik.handleChange}
				/>
			</div>
		</div>
	);
};

export default Recipient;
